body {
  font-family: 'Source Sans Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
.logstats-name {
  font-weight: bold;
  font-size: 20px;
  color: #00A65A;
  vertical-align: bottom;
  margin-left: 20px;
  margin-bottom: 10px;
}
.step {
  color: #00A65A;
  font-size: 35px;
  text-decoration: underline;
}
.next {
  float: right;
}
.previos {
  float: left;
}
nav li.active {
  font-weight: bold;
  color: #00A65A;
}
table.database-setup tr[data-databases] {
  display: none;
}
table.database-setup tr[data-databases~='mysql'] {
  display: table-row;
}
table.database-setup td {
  vertical-align: middle !important;
  width: 50%;
}
.select2-container {
  width: 300px;
}
